* {
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
 }
 
 
 @font-face {
    font-family: PoppinsBold;
    src: url("../Fonts/Poppins-Bold.ttf");
 }
 
 
 @font-face {
    font-family: PoppinsMedium;
    src: url("../Fonts/Poppins-Medium.ttf");
 }
 
 
 nav {
    div {
        .mainLogo {
            font-family: PoppinsBold;
            color: black;
        }
        button{
            span{
                color: #000000;
            }
        }
        div {
            font-weight: 200;
            display: flex;
            justify-content: center;
 
 
            ul {
                color: #000000;
 
 
                li {
                    margin: 0 30px 0 0;
                    color: black;
 
 
                    .nav-link {
                        font-family: PoppinsMedium;
                        color: #3d3d3d;
 
 
                        &:hover {
                            color: #007be5;
                            transition: all 0.5s;
                        }
                    }
 
 
                    a {
                        font-size: 15px;
                        letter-spacing: 0.5px;
                        cursor: pointer;
                        position: relative;
                        color: black;
 
 
                        &::after {
                            content: "";
                            width: 2px;
                            height: 1px;
                            color: #000000;
 
 
                        }
                    }
                }
            }
        }
 
 
        .buttons {
            margin: 0 0px 0 0;
            border-radius: 5px;
            font-family: PoppinsMedium;
 
 
            .LoginBtn {
                border-radius: 5px;
                border: 1px solid #007be5;
 
 
                button {
                    color: #007be5;
                }
            }
            .signUpBtn {
                border-radius: 5px;
                button {
                    color: #FFFFFF;
                }
            }
        }
    }
 }
 @media only screen and (max-width:992px){
    .buttons{
        display: none;
    }
 }
 